import React from "react"
// Libs
import getPageData from "globalLibs/helpers/get-page-data"
import getWorkExperience from "projectLibs/helpers/get-work-experience"
// Atoms
import Seo from "globalAtoms/Seo"
// Organisms
import Layout from "globalOrganisms/Layout"
import HomePageCover from "projectOrganisms/HomePageCover"
import Sidebar from "projectOrganisms/Sidebar"
import MainNav from "projectOrganisms/MainNav"

export default function IndexPage(props) {
  const pageData = getPageData(props)

  return (
    <Layout className="flex-col">
      <Seo pageData={pageData} />
      <Sidebar />
      <MainNav />
      <HomePageCover style={{ height: "400px" }} avatar>
        <div
          className="flex flex-col text-center absolute z-10 inset-0 text-hight-contrast"
          style={{ top: "24%" }}
        >
          <span className="xxxLarge-high-title font-bold mb-4">
            Senior UX/UI Designer
          </span>
          <span className="xxxLarge-h1">Middle Web Developer</span>
        </div>
      </HomePageCover>
      <article className="container mx-auto max-w-screen-lg mt-32">
        <div className="flex justify-between items-end">
          <h1 className="m-0">My way</h1>
          <span className="text-tint-foreground-purple">
            {getWorkExperience()} years of experience
          </span>
        </div>
        <hr />
        <ul>
          <li>
            <span className="text-tint-foreground-purple">[2008 - 2015]</span> -
            Received education. Work on freelance exchanges. Cooperation with
            Ukrainian and Russian web studios.
          </li>
          <li>
            <span className="text-tint-foreground-purple">[2015 - 2016]</span> -
            Worked in a Belarusian web studio as Middle UX/UI Designer.
          </li>
          <li>
            <span className="text-tint-foreground-purple">[2016]</span> -
            Established cooperation with regular customers and distanced himself
            from freelance exchanges.
          </li>
          <li>
            <span className="text-tint-foreground-purple">[2017]</span> - I have
            developed my own design system. Deepened his knowledge in the field
            of user behavior analysis and conversion optimization for e-commerce
            sites. Learned HTML5, CSS3. Began to be responsible for quality
            control of Front-end development on several projects. Started
            working for Upwork.
          </li>
          <li>
            <span className="text-tint-foreground-purple">[2018]</span> - Began
            to specialize in the most complex projects. Developed UX/UI for
            mapping services in Ukraine and Belarus. Perfectly mastered Google
            and Apple Guidlines. Added a lot of patterns to my design system for
            developing mobile app design. Continued to work for Upwork.
          </li>
          <li>
            <span className="text-tint-foreground-purple">[2019]</span> -
            Deepened my knowledge in Front-end development. I started learning
            Java Script, React and Node JS. I transferred my design system from
            Sketch to Figma, significantly expanded its capabilities and
            applications. Refused to work for Upwork in favor of working with
            regular clients.
          </li>
          <li>
            <span className="text-tint-foreground-purple">[2020]</span> - I
            wrote a component library in React for my design system. Moved most
            of my projects to my own codebase. I started working as a Team Lead
            in a team that was developing a design system for a number of
            microservices. Main responsibilities: component quality control,
            code review, minimizing the time spent on development.
          </li>
          <li>
            <span className="text-tint-foreground-purple">[NOW]</span> - I
            continue to work as a Team Lead.
          </li>
        </ul>
      </article>
    </Layout>
  )
}
